import { FC } from 'react';

import { Column, Grid, Row, OffGrid } from '../Grid';
import { Main } from '../Layout.styles';
import { LayoutProps, OneColumnLayoutProps } from '../Layout.types';

/**
 * OneColumnNarrow is a layout component that provides a single column layout
 * that does not account for any sidebar.
 */
const OneColumnNarrow: FC<OneColumnLayoutProps> = ({ children }) => {
  return (
    <Grid>
      <Row>
        <Column
          span={{ mobile: 4, tablet: 6, laptop: 8 }}
          offset={{ tablet: 1, laptop: 2 }}
        >
          <Main>{children}</Main>
        </Column>
      </Row>
    </Grid>
  );
};

export default OneColumnNarrow;

/** FullBleed containers will stretch from edge to edge of a <Grid> */
export const FullBleedContainer: FC<LayoutProps> = ({ children }) => (
  <OffGrid>{children}</OffGrid>
);

/** InsetLeft containers will stretch from the left edge of a <Grid> to the middle of the column */
export const InsetLeftContainer: FC<LayoutProps> = ({ children }) => (
  <OffGrid align="left" span={{ tablet: 3, laptop: 4 }}>
    {children}
  </OffGrid>
);

/** InsetRight containers will stretch from the middle of a column to the right edge of a <Grid> */
export const InsetRightContainer: FC<LayoutProps> = ({ children }) => (
  <OffGrid align="right" span={{ tablet: 3, laptop: 4 }}>
    {children}
  </OffGrid>
);
